html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  max-width: 110rem;
  margin: auto;
}

.espacio {
  width: 100%;
  margin-bottom: 7rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 15rem;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 600;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
  margin: 0;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
  border-radius: 1rem;
}
.alerta.error {
  background-color: #BA0534;
}
.alerta.exito {
  background-color: #9F425A;
}

.header {
  background-color: #CF5A60;
}
.header .header_cont {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .header_cont .navegacion {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.header .header_cont .navegacion a {
  color: #ffffff;
}
.header .header_cont .redes {
  display: flex;
  gap: 2rem;
}
.header .header_cont .redes .redes_div {
  background-color: #ffffff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
}
.header .header_cont .redes .redes_div a i {
  color: #CF5A60;
}

.imagen_header {
  width: 100%;
  height: 60rem;
}
.imagen_header .img_header {
  width: 100%;
  height: 60rem;
}
.imagen_header .imagen_header_fondo {
  position: absolute;
  top: 0rem;
  width: 100%;
  height: 60rem;
  background-color: hsla(0, 0%, 8%, 0.524);
}
.imagen_header .logo_header {
  position: absolute;
  top: 20rem;
  left: 43rem;
  margin: 0;
  z-index: 2;
  width: 50rem;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.slideshow {
  width: 100%;
  height: 60rem;
  position: relative;
  overflow: hidden;
}
.slideshow ul, .slideshow ol {
  list-style: none;
  margin: 0;
}

.slideshow2 {
  width: 100%;
  height: 40rem;
  position: relative;
  overflow: hidden;
}
.slideshow2 ul, .slideshow2 ol {
  list-style: none;
  margin: 0;
}

.slider li, ul,
.slider2 li, ul {
  width: 100%;
}

.slider li,
.slider2 li {
  overflow: hidden;
}

.slider li .img_slider {
  width: 100%;
  height: 60rem;
  overflow: hidden;
  animation-duration: 4s;
  animation-name: slidein;
  object-fit: cover;
}

.slider2 li .img_slider {
  width: 100%;
  height: 40rem;
  overflow: hidden;
  animation-duration: 4s;
  border-radius: 3px;
}

@keyframes slidein {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.2);
  }
}
.slider .caption {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 15px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  z-index: 1;
}

.slider .caption h1 {
  font-size: 50px;
}

.slider .caption p {
  margin-top: 10px;
  font-size: 20px;
}

.pagination {
  position: absolute;
  top: 102%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.pagination li {
  font-size: 20px;
  margin: 2px 5px;
  color: #909196;
  cursor: pointer;
}

.left, .right {
  position: absolute;
  top: 0;
  height: 100%;
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 35px;
  cursor: pointer;
  z-index: 2;
}

.left {
  left: 10px;
}

.right {
  right: 10px;
}

.total {
  margin-bottom: 10px;
  border: 0;
}

.slider_eventos {
  position: relative;
}

.slideshow {
  overflow: hidden;
}

.slider2 {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.slider2 li {
  flex: 0 0 100%;
}

.img_slider {
  width: 100%;
  height: 40rem;
  object-fit: cover;
}

.imagen_header2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.imagen_header_fondo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /* Cambia el fondo segÃºn tu preferencia */
}

.slider2:hover .imagen_header2 {
  opacity: 1;
}

.texto_overlay {
  color: white;
  font-size: 2rem;
  text-align: center;
  max-width: 80%; /* Ajusta el ancho mÃ¡ximo del texto segÃºn tus necesidades */
  max-height: 80%; /* Ajusta la altura mÃ¡xima del texto segÃºn tus necesidades */
  overflow: hidden; /* Recorta el texto si es demasiado largo */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.texto_overlay span {
  font-weight: 600;
}

.imagen_header2 {
  width: 50%;
  height: 40rem;
}
.imagen_header2 .imagen_header_fondo {
  position: absolute;
  top: 0rem;
  width: 100%;
  height: 40rem;
  background-color: hsla(0, 0%, 8%, 0.524);
}
.imagen_header2 .logo_header {
  position: absolute;
  top: 20rem;
  left: 43rem;
  margin: 0;
  z-index: 2;
  width: 50rem;
}

#btnWs {
  padding: 10px;
  background-color: green;
  color: white;
}

@media screen and (max-width: 600px) {
  .slider .caption p {
    display: none;
  }
  .slider .caption h1 {
    font-size: 35px;
  }
  .left, .right {
    font-size: 30px;
  }
  .slider li img {
    width: 600px;
  }
  .pagination {
    display: none;
  }
}
/** Media Queries **/
.footer {
  background-color: #000;
}
.footer .footer_todo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 2rem;
}
.footer .footer_todo .logo_footer {
  width: 45rem;
}
.footer .footer_todo .footer_titulo {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer .footer_todo .footer_titulo h5 {
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
}
.footer .footer_todo .copyright {
  padding: 5rem 2rem 2rem 2rem;
  font-size: 1.2rem;
  color: #ffffff;
}

.footer_info {
  display: grid;
  justify-content: space-between;
  width: 100%;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 5rem;
}
.footer_info .footer_info_part p {
  color: #ffffff;
  font-size: 1.5rem;
}
.footer_info .redes_part_footer {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 0rem 3rem;
}
.footer_info .redes_part_footer .redes_div a i {
  color: #ffffff;
}

